.navbar {
    height: 80px;
    margin-left: 16vw;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 999;
  }
  
  .sparrow {
    height:50px;
  }

  .navbarContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80px;
    width: 67vw;
  }
  
  .navbarLogo {
    color: #fff;
    justify-self: start;
    margin-left: 15px;
    cursor: pointer;
    /* text-decoration: none; */
    font-size: 2rem;
    display: flex;
    align-items: center;
  }
  
  .fa-typo3 {
    margin-left: 0.5rem;
    font-size: 1.8rem;
  }
  
  .navMenu {
    display: flex;
    list-style: none;
    text-align: center;
    justify-content: end;
  }
  
  .navItem {
    height: 80px;
    margin: 0 15px;
  }
  
  .navLinks {
    color: var(--text);
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
    font-size: 16px;
  }
  
  .navLinks:hover {
    color: #129AA6;
    /* border-bottom: 4px solid #000; */
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: #fff;
  }
  
  .navLinksMobile {
    display: none;
  }
  
  .menuIcon {
    display: none;
  }

  .title{
    display: flex;
    min-width: 15%;
    color: var(--text);
    font-size: 28px;
    align-items: center;
    justify-content: space-between;
  }
  
  .moneger {
    text-decoration: none !important;
    color: white impo !important;
  }

  @media screen and (max-width: 960px) {
    .navbar {
      position: absolute;
      height: 100vh;
      overflow: hidden;
      width: 100vw;
      margin-left: 0;
      /* padding-left: 10vw; */
    }

    .navbarContainer{
      padding-left: 10vw;
    }

    .navItem{
      overflow: hidden;
    }
  
    .navMenu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .navMenu.active {
      background: #242222;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 200;
      overflow: hidden;
      height: 100vh;
    }
  
    .navLinks {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .navLinks:hover {
      background-color: #fff;
      color: #242424;
      border-radius: 0;
    }
  
    .navbar-logo {
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(25%, 50%);
    }
  
    .menuIcon {
      display: block;
      position: absolute;
      top: 10px;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
      padding-right: 5vw;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  
    .navLinksMobile {
      display: block;
      text-align: center;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      text-decoration: none;
      font-size: 1.5rem;
      background-color: transparent;
      color: #fff;
      padding: 14px 20px;
      border: 1px solid #fff;
      transition: all 0.3s ease-out;
    }
  
    .navLinksMobile:hover {
      background: #fff;
      color: #242424;
      transition: 250ms;
    }
  }
