.hero2 {
    height: 200vh;
    width: 100vw;
    overflow: hidden;
    position: relative;
}

.bgWrapper{ 
    content: "";
    position: absolute;
    width: 300%;
    height: 200%;
    background: #566976;
    transform: rotate(-5deg);
    z-index: 1;
}

.container {
    padding-top: 30vh;
    width:100vw;
    position: absolute;
    z-index: 100;
    padding-left: 5vw;
    padding-right: 5vw;
}

.keyFeatures{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: space-between;
}

.keyFeatures >h3{
    font-size:59px;
    color: var(--text);
    margin-bottom: var(--margins);
    text-align: center;
}

.keyFeatures >p{
    font-size:16px;
    color: var(--text);
    /* margin: var(--margins);  */
    max-width: 30vw;
    text-align: center;
}

.allFeatures {
    display: flex;
    /* justify-content: center; */
    margin: 4vw 15%;
}

.featureLists, .preview{
    width: 50%;
}

.featureLists {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.featureTitle {
    font-size: 28px;
    color: var(--text);
    margin-bottom: var(--margins);
}

.featureCaption {
    font-size:16px;
    color: var(--text);
}

.features {
    display: flex;
    transform: scale(0.9);
    padding:20px;
    border-radius: 20px;
    /* justify-content: flex-start; */
}

.features:hover {
    transition: all 0.3s ease-out;
    background: #ACBAC3;
    cursor:pointer;
}

/* .featureLogo{
    height:8vw;
    width:8vw;
    min-width: 30%;
} */

.featureText {
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-width:70%;
}

@media screen and (max-width:1250px) {
    .keyFeatures >p{
        min-width: 50vw;
    }
    .preview{
        display: none;
    }

    .featureLists{
        width: 100vw;
    }

    .features{
        background: #ACBAC3;
    }

}

@media screen and (max-width:750px) {
    .keyFeatures >p{
        min-width: 80vw;
    }

    .preview{
        display: none;
    }

    .featureLists{
        width: 100vw;
    }
    .allFeatures{
        min-width: 80vw;
        margin: 0;
    }

    .features{
        padding-bottom: 20px;
        background: #ACBAC3;
    }
}