

.btn {
    padding: 15px 20px;
    height: 55px;
    width: 120px;
    border-radius: 20px;
    margin-bottom: 2vh;
    outline: none;
    border: none;
    cursor: pointer;
}

.btnPrimary{
    background-color: var(--primary);
    color: var(--text);
    width: auto;
}

.btnSecondary{
    background-color: var(--primary);
    color: var(--text);
    border-radius: 2px;
}

.btnOutline{
    background-color: transparent;
    color: var(--primary);
    border-radius: 2px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btnOutline:hover{
    color: var(--text);
    background-color: var(--primary);
}

.btnPrimary:hover{
    background-color: #129AA6;
    transition: all 0.3s ease-out;
}